html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  background: #f0f0f0;
  color: #000;
  text-decoration: none;
}
a:hover {
  background: transparent;
  color: #f0f0f0;
}
h1 {
  font: 4em/1.1em Helvetica, Arial;
  font-weight: bold;
  margin: 0 0 0.5em 0;
}
h2 {
  font: 1.5em/1.1em Helvetica, Arial;
  font-weight: bold;
  margin: 0 0 0.8em 0;
}
p {
  margin: 0 0 1.3em 0;
}
p.meta {
  font-size: 0.85em;
  line-height: 1.5em;
}
i {
  font: 1em/1em Inconsolata, Courier, Menlo;
}
b,
strong {
  font-weight: bold;
}
ul.steps {
  list-style-type: disc;
  font-size: 0.9em;
  line-height: 1.7em;
}
ul.steps li {
  padding: 0 0 0 2%;
}
body {
  margin: 0;
  padding: 0;
  background: #000;
  color: #f0f0f0;
  font-family: Helvetica, Arial;
  font-size: 18px;
  line-height: 1.5em;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: none;
}
@media only screen and (min-width: 800px) {
  body {
    background: #800080;
  }
}
@media only screen and (min-width: 1024px) {
  body {
    background: #00f;
  }
}
div#lol {
  background: #f00;
}
.monkey {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("../../assets/gfx_photo_monkey.gif");
  background-attachment: fixed;
  background-size: cover;
  z-index: -100;
}
.monkey.scrolled {
  display: none;
}
.intro {
  position: fixed;
  top: 0;
  left: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  height: 60 vH;
  width: 100%;
  padding: 15px;
  z-index: -99;
}
.content {
  margin: 40% 0 0 0;
  display: block;
  width: 100%;
}
body.scrolled .monkey {
  display: none;
}
body.scrolled .content {
  background: #000;
}
.row {
  overflow: auto;
  padding: 15px;
}
.logo-bleed {
  width: 100px;
  height: 38px;
  background-size: 100% auto;
  background-repeat: no-repeat;
  margin: 0 0 15px 0;
}
.logo-bleed.white {
  background-image: url("../../assets/logo_bleed-white.svg");
}
.logo-bleed.black {
  background-image: url("../../assets/logo_bleed-black.svg");
}
